.owerlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    height: 300px;
    border-radius: 10px;
    background-color: #fff;
    padding: 10px;
}

.close {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 16px;
    font-weight: 700;
    padding: 5px;
    cursor: pointer;
}

label {
    display: block;
    padding: 10px;
}

.add {
    background-color: yellowgreen;
    padding: 16px;
    margin-left: 5px;
    border: none;
    font-size: 15px;
    color: #fff;
    font-weight: 700;
}

.input {
    padding: 1rem;
}